import { RestProvider } from './../../providers/rest/rest';
import { LoadingProvider } from './../../providers/loading/loading';
import { Component, Input } from '@angular/core';
import { IonicPage, NavController, AlertController, NavParams } from 'ionic-angular';
import { GlobalProvider } from '../../providers/global/global';

/**
 * Generated class for the PasswordCodePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-password-code',
  templateUrl: 'password-code.html',
})
export class PasswordCodePage {
  @Input() events: any;

  public code: string;

  public errorCodeEmpty: boolean;

  constructor(
    public navCtrl: NavController, 
    public loading: LoadingProvider,
    public global: GlobalProvider, 
    public alertCtrl: AlertController,
    public rest: RestProvider,
    public navParams: NavParams
    ) {
      this.errorCodeEmpty= false;

      this.events =  {
        "onSendcode" : this.onSendcode
      }
  }

  onEvent = (event: string): void => {
    if (event == "onSendcode" && !this.validate()) {
        return ;
    }
    if (this.events[event]) {
        this.events[event]({
            'code': this.code,
        });
    }        
  }

  validate():boolean {
    this.errorCodeEmpty= false;

    if (!this.code ||this.code.length == 0) {
        this.errorCodeEmpty = true;
    }
    
    return !this.errorCodeEmpty;
  }

  // login and go to home page
  onSendcode = (params): void => {
    this.loading.show();
    let token = this.navParams.get('token');
    this.rest.checkResetPasswordCode(this.code, token).then(
      (scs:any) => {
        // console.log(scs.token);
        // let token = scs.token;
        this.navCtrl.push('PasswordChangePage', {token: scs.token});
      },
      (err) => {
        this.loading.hide();
        
        let message = "Không thể kết nối tới máy chủ!";
        if (err.error.error == 'token_not_found'){
          message = "Không tìm thấy token!"
        }
        if (err.error.error == 'code_not_format' || err.error.error == 'code_fail'){
          message = "Mã xác thực không hợp lệ!"
        }
        if (err.error.error == 'token_expries'){
          message = "Mã xác thực đã hết hạn sử dụng!"
        }
        if (err.error.error == 'input_max_5_code'){
          message = "Nhập quá số lần cho phép! Vui lòng liên hệ ban quản trị để được hỗ trợ thêm."
        }
        
        let alert = this.alertCtrl.create({
          title: 'Lỗi',
          message: message,
          buttons: [
            {
              text: 'OK',
              role: 'ok',
            }
          ]
        });
        alert.present();
      }
    )
    

  }

  openLoginPage() {
    this.navCtrl.setRoot('LoginPage');
  }

}



